@font-face {
    font-family: 'RSFontInput';
    src:  url('fonts/otf/RuneScape-Plain-11.otf') format('opentype'),
          url('fonts/ttf/RuneScape-Plain-11.ttf') format('truetype');
}

@font-face {
    font-family: 'RSFont';
    src:  url('fonts/otf/RuneScape-Quill-8.otf') format('opentype'),
          url('fonts/ttf/RuneScape-Quill-8.ttf') format('truetype');
}

html, body, p {
    padding: 0;
    margin: 0;

    font-family: "RSFont", 'Courier New', Courier, monospace;
}

p {
    text-align: center;
}

input {
    text-align: center;
    -moz-appearance: textfield;

    font-family: "RSFontInput";
    font-size: 1.1em;
}

.inputField {
    width: 150px;

}

.Title {
    display: flex;
    align-items: center;
    justify-content: center;
}

.center {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Spacing {
    margin-bottom: 10px;
}

.inputField {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    
}

input {
    background-color: whitesmoke;
    border: 2px solid black;
    border-radius: 5px;
    
    margin: 3px;
}

.button {
    width: 45px;
}